<template>
  <div class="books content-container">
    <h1 class="books-title">My Books</h1>
    <div class="controls">
      <div class="sort-container">
        <i class="fas fa-sort"></i> Sort:
        <button @click="cycleSortOption" class="sort-button">
          {{ sortOptionLabel }}
        </button>
      </div>
      <div class="view-toggle">
        View: 
        <button @click="toggleView" class="view-button">
          <i :class="['fas', isListView ? 'fa-th-large' : 'fa-list']"></i>
        </button>
      </div>
    </div>
    <div v-for="section in sections" :key="section.title" class="book-section">
      <h2 class="section-title">{{ section.title }}</h2>
      <ul :class="['book-list', { 'cover-view': !isListView }]">
        <li v-for="book in section.books" :key="book.isbn" class="book-item">
          <div class="book-summary" @click="navigateToBookDetails(book.isbn)">
            <img v-if="book.image" :src="book.image" :alt="book.title + ' cover'" class="book-cover">
            <div v-else class="book-cover-placeholder">
              <i class="fas fa-book"></i>
            </div>
            <div class="book-info">
              <div class="book-title">{{ book.title }}</div>
              <div class="book-author">{{ book.author }}</div>
              <div v-if="book.progress > 0 && book.progress < 100" class="progress-container">
                <div class="progress-bar">
                  <div class="progress" :style="{ width: `${book.progress}%` }"></div>
                </div>
                <span class="progress-text">{{ book.progress }}%</span>
              </div>
              <div v-if="book.progress === 100" class="completion-date">
                Completed on: {{ formatDate(book.dateRead) }}
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, nextTick } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'Books',
  setup() {
    const store = useStore()
    const books = ref([])
    const sortOptions = ['title', 'author', 'progress', 'dateRead']
    const sortOptionIndex = ref(0)
    const isListView = ref(true)

    const sortOption = computed(() => sortOptions[sortOptionIndex.value])
    const sortOptionLabel = computed(() => {
      const labels = {
        title: 'Title',
        author: 'Author',
        progress: 'Progress',
        dateRead: 'Date Read'
      }
      return labels[sortOption.value]
    })
    const getSortIcon = () => {
      switch (sortOption.value) {
        case 'title':
          return 'fa-sort-alpha-down'
        case 'author':
          return 'fa-user'
        case 'progress':
          return 'fa-tasks'
        case 'dateRead':
          return 'fa-calendar-alt'
        default:
          return 'fa-sort'
      }
    }

    const getSectionIcon = (sectionTitle) => {
      switch (sectionTitle) {
        case 'Currently Reading':
          return 'fa-book-reader'
        case 'Want to Read':
          return 'fa-list'
        case 'Finished':
          return 'fa-check-circle'
        default:
          return 'fa-book'
      }
    }
    onMounted(() => {
      store.dispatch('content/loadBooks')
      books.value = store.getters['content/getBooks'].map(book => ({
        ...book,
        showDetails: false
      }))
      nextTick(() => {
        const progressBars = document.querySelectorAll('.progress')
        progressBars.forEach(bar => {
          const targetWidth = bar.style.width
          bar.style.width = '0%'
          setTimeout(() => {
            bar.style.width = targetWidth
          }, 50)
        })
      })
    })

    const formatDate = (dateString) => {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }

    const toggleDetails = (book) => {
      book.showDetails = !book.showDetails
    }

    const cycleSortOption = () => {
      sortOptionIndex.value = (sortOptionIndex.value + 1) % sortOptions.length
    }

    const toggleView = () => {
      isListView.value = !isListView.value
    }

    const sortBooks = () => {
      books.value.sort((a, b) => {
        switch (sortOption.value) {
          case 'title':
            return a.title.localeCompare(b.title)
          case 'author':
            return a.author.localeCompare(b.author)
          case 'progress':
            return b.progress - a.progress
          case 'dateRead':
            return new Date(b.dateRead || 0) - new Date(a.dateRead || 0)
          default:
            return 0
        }
      })
    }
    const sections = computed(() => {
      sortBooks()
      return [
        {
          title: 'Currently Reading',
          books: books.value.filter(book => book.progress > 0 && book.progress < 100)
        },
        {
          title: 'Want to Read',
          books: books.value.filter(book => book.progress === 0)
        },
        {
          title: 'Finished',
          books: books.value.filter(book => book.progress === 100)
        }
      ]
    })

    return {
      sections,
      sortOptionLabel,
      isListView,
      formatDate,
      toggleDetails,
      cycleSortOption,
      toggleView,
      getSortIcon,
      getSectionIcon
    }
  }
}
</script>
